<template>
  <div class="panel-tab__content">
    <div class="element-property list-property">
      <el-table :data="ownerListenersList" size="mini" border fit>
        <el-table-column label="" width="50px" type="index" />
        <el-table-column label="" min-width="100px" prop="event" />
        <el-table-column
          label=""
          min-width="100px"
          show-overflow-tooltip
          :formatter="row => listenerTypeObject[row.listenerType]"
        />
        <el-table-column label="" width="100px">
          <template slot-scope="{ row, $index }">
            <el-button size="mini" type="text" @click="openListenerForm(row, $index)"></el-button>
            <el-divider direction="vertical" />
            <el-button size="mini" type="text" style="color: #ff4d4f" @click="removeListener(row, $index)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="element-listener-add__button">
      <el-button size="small" type="primary" icon="el-icon-plus" @click="openListenerForm(null)"></el-button>
    </div>

    <el-drawer :visible.sync="showListenerForm" title="" :size="`${this.width}px`" append-to-body>
      <el-form size="small" :model="listenerForm" label-width="96px" ref="listenerFormRef">
        <el-form-item label="" prop="event" :rules="{ required: true, trigger: ['blur', 'change'] }">
          <el-select v-model="listenerForm.event">
            <el-option label="start" value="start" />
            <el-option label="end" value="end" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="listenerType" :rules="{ required: true, trigger: ['blur', 'change'] }">
          <el-select v-model="listenerForm.listenerType">
            <el-option v-for="i in Object.keys(listenerTypeObject)" :key="i" :label="listenerTypeObject[i]" :value="i" />
          </el-select>
        </el-form-item>
        <el-form-item
          label="Java"
          prop="class"
          v-if="listenerForm.listenerType === 'classListener'"
          key="listener-class"
          :rules="{ required: true, trigger: ['blur', 'change'] }"
        >
          <el-input v-model="listenerForm.class" clearable />
        </el-form-item>
        <el-form-item
          label=""
          prop="expression"
          v-if="listenerForm.listenerType === 'expressionListener'"
          key="listener-expression"
          :rules="{ required: true, trigger: ['blur', 'change'] }"
        >
          <el-input v-model="listenerForm.expression" clearable />
        </el-form-item>
        <el-form-item
          label=""
          prop="delegateExpression"
          v-if="listenerForm.listenerType === 'delegateExpressionListener'"
          key="listener-delegate"
          :rules="{ required: true, trigger: ['blur', 'change'] }"
        >
          <el-input v-model="listenerForm.delegateExpression" clearable />
        </el-form-item>
        <template v-if="listenerForm.listenerType === 'scriptListener'">
          <el-form-item
            label=""
            prop="scriptFormat"
            key="listener-script-format"
            :rules="{ required: true, trigger: ['blur', 'change'] }"
          >
            <el-input v-model="listenerForm.script.scriptFormat" clearable />
          </el-form-item>
          <el-form-item
            label=""
            prop="script.scriptType"
            key="listener-script-type"
            :rules="{ required: true, trigger: ['blur', 'change'] }"
          >
            <el-select v-model="listenerForm.script.scriptType">
              <el-option label="" value="inlineScript" />
              <el-option label="" value="externalScript" />
            </el-select>
          </el-form-item>
          <el-form-item
            label=""
            prop="script.value"
            v-if="listenerForm.script.scriptType === 'inlineScript'"
            key="listener-script"
            :rules="{ required: true, trigger: ['blur', 'change'] }"
          >
            <el-input v-model="listenerForm.script.value" clearable />
          </el-form-item>
          <el-form-item
            label=""
            prop="script.resource"
            v-if="listenerForm.script.scriptType === 'externalScript'"
            key="listener-resource"
            :rules="{ required: true, trigger: ['blur', 'change'] }"
          >
            <el-input v-model="listenerForm.script.resource" clearable />
          </el-form-item>
        </template>
      </el-form>

      <el-divider />
      <p class="listener-filed__title">
        <span><i class="el-icon-menu"></i></span>
        <el-button size="mini" type="primary" @click="openListenerFieldForm(null)"></el-button>
      </p>
      <el-table :data="fieldsOfListener" size="mini" border fit style="flex: none">
        <el-table-column label="" width="50px" type="index" />
        <el-table-column label="" min-width="100px" prop="name" />
        <el-table-column
          label=""
          min-width="80px"
          show-overflow-tooltip
          :formatter="row => fieldTypeObject[row.fieldType]"
        />
        <el-table-column
          label="/"
          min-width="100px"
          show-overflow-tooltip
          :formatter="row => row.string || row.expression"
        />
        <el-table-column label="" width="100px">
          <template slot-scope="{ row, $index }">
            <el-button size="mini" type="text" @click="openListenerFieldForm(row, $index)"></el-button>
            <el-divider direction="vertical" />
            <el-button size="mini" type="text" style="color: #ff4d4f" @click="removeListenerField(row, $index)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="listener-form-slider" style="flex: 1"></div>
      <div class="element-listener-add__button">
        <el-button size="small" @click="handleCancel"> </el-button>
        <el-button size="small" type="primary" @click="saveListenerConfig"> </el-button>
      </div>
    </el-drawer>

    <el-dialog title="" :visible.sync="showListenerFieldForm" width="600px" append-to-body destroy-on-close>
      <el-form :model="listenerFieldForm" size="small" label-width="96px" ref="listenerFieldFormRef">
        <el-form-item label="" prop="name" :rules="{ required: true, trigger: ['blur', 'change'] }">
          <el-input v-model="listenerFieldForm.name" clearable />
        </el-form-item>
        <el-form-item label="" prop="fieldType" :rules="{ required: true, trigger: ['blur', 'change'] }">
          <el-select v-model="listenerFieldForm.fieldType">
            <el-option v-for="i in Object.keys(fieldTypeObject)" :key="i" :label="fieldTypeObject[i]" :value="i" />
          </el-select>
        </el-form-item>
        <el-form-item
          v-if="listenerFieldForm.fieldType === 'string'"
          label=""
          prop="string"
          key="field-string"
          :rules="{ required: true, trigger: ['blur', 'change'] }"
        >
          <el-input v-model="listenerFieldForm.string" clearable />
        </el-form-item>
        <el-form-item
          v-if="listenerFieldForm.fieldType === 'expression'"
          label=""
          prop="expression"
          key="field-expression"
          :rules="{ required: true, trigger: ['blur', 'change'] }"
        >
          <el-input v-model="listenerFieldForm.expression" clearable />
        </el-form-item>
      </el-form>
      <template slot="footer">
        <el-button size="mini" @click="showListenerFieldForm = false"> </el-button>
        <el-button size="mini" type="primary" @click="saveListenerFiled"> </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ElementListener",
  props: {
    listeners: {
      type: Array,
      default: () => []
    },
    bpmnModeler: Object,
    elementId: String
  },
  inject: {
    prefix: "prefix",
    width: "width"
  },
  data() {
    return {
      ownerListenersList: [],
      ownerListenersObjectList: [],
      listenerForm: {},
      listenerFieldForm: {},
      fieldsOfListener: [],
      showListenerForm: false,
      listenerIndex: 0,
      showListenerFieldForm: false,
      listenerFiledIndex: 0,
      listenerTypeObject: {
        classListener: "Java ",
        expressionListener: "",
        delegateExpressionListener: "",
        scriptListener: ""
      },
      fieldTypeObject: {
        string: "",
        expression: ""
      }
    };
  },
  watch: {
    listeners: {
      deep: true,
      handler: function(newVal) {
        if (newVal.length) {
          this.ownerListenersObjectList = newVal;
          this.ownerListenersList = newVal.map(li => {
            let listenerType;
            if (li.class) listenerType = "classListener";
            if (li.expression) listenerType = "expressionListener";
            if (li.delegateExpression) listenerType = "delegateExpressionListener";
            if (li.script) listenerType = "scriptListener";
            return {
              ...li,
              listenerType: listenerType
            };
          });
        } else {
          this.ownerListenersObjectList = [];
          this.ownerListenersList = [];
        }
      }
    }
  },
  created() {
    this.initModel();
  },
  methods: {
    // 
    initModel() {
      if (!this.bpmnModeler) {
        this.timer = setTimeout(() => this.initModel(), 10);
        return;
      }
      if (this.timer) clearTimeout(this.timer);
      this.moddle = this.bpmnModeler.get("moddle");
    },
    // 
    openListenerForm(listener, index) {
      if (listener) {
        let listenerFormS = JSON.parse(JSON.stringify(listener));
        if (listener.script) {
          Object.assign(listenerFormS.script, {
            ...listener.script,
            scriptType: listener.script.resource ? "externalScript" : "inlineScript"
          });
        }
        for (let key in listenerFormS) {
          this.$set(this.listenerForm, key, listenerFormS[key]);
        }
        this.listenerIndex = index;
      } else {
        this.listenerForm = {};
        this.listenerIndex = -1;
      }
      this.initListenerFields();
      this.showListenerForm = true;
      this.$nextTick(() => {
        if (this.$refs["listenerFormRef"]) this.$refs["listenerFormRef"].clearValidate();
      });
    },
    // 
    handleCancel() {
      this.showListenerForm = false;
      this.listenerForm = {};
      this.listenerScriptForm = {};
    },
    // 
    async saveListenerConfig() {
      let validateStatus = await this.$refs["listenerFormRef"].validate();
      if (!validateStatus) return; // 
      // 1. 
      const listenerObj = this.initListenerObject(this.listenerForm);
      const listenerModel = this.moddle.create(`${this.prefix}:ExecutionListener`, listenerObj);
      this.$emit("listener-save", listenerModel);
      // 2. 
      if (this.listenerIndex === -1) {
        this.ownerListenersObjectList.push(listenerModel);
        this.ownerListenersList.push(this.listenerForm);
      } else {
        this.ownerListenersObjectList.splice(this.listenerIndex, 1, listenerModel);
        this.ownerListenersList.splice(this.listenerIndex, 1, this.listenerForm);
      }
      // 3. 
      this.$emit("change", this.ownerListenersObjectList);
      // 4. 
      this.showListenerForm = false;
      this.listenerForm = {};
    },
    initListenerObject(options) {
      const listenerObj = {};
      listenerObj.event = options.event;
      switch (options.listenerType) {
        case "scriptListener":
          listenerObj.script = this.moddle.create(`${this.prefix}:Script`, { ...this.listenerScriptForm });
          break;
        case "expressionListener":
          listenerObj.expression = options.expression;
          break;
        case "delegateExpressionListener":
          listenerObj.delegateExpression = options.delegateExpression;
          break;
        default:
          listenerObj.class = options.class;
      }
      if (options.fields) {
        listenerObj.fields = options.fields.map(field => {
          return this.moddle.create(`${this.prefix}:Field`, field);
        });
      }
      return listenerObj;
    },
    // 
    initListenerFields() {
      if (this.listenerForm.fields && this.listenerForm.fields.length) {
        this.fieldsOfListener = this.listenerForm.fields.map(field => {
          return {
            ...field,
            fieldType: field.string ? "string" : "expression"
          };
        });
        return;
      }
      this.fieldsOfListener = [];
    },
    // 
    openListenerFieldForm(filed, index) {
      this.listenerFieldForm = filed ? JSON.parse(JSON.stringify(filed)) : {};
      this.listenerFiledIndex = filed ? index : -1;
      this.showListenerFieldForm = true;
      this.$nextTick(() => {
        if (this.$refs["listenerFieldFormRef"]) this.$refs["listenerFieldFormRef"].clearValidate();
      });
    },
    // 
    async saveListenerFiled() {
      let validateStatus = await this.$refs["listenerFieldFormRef"].validate();
      if (!validateStatus) return; // 
      const filedObj =
        this.listenerFieldForm.fieldType === "string"
          ? { name: this.listenerFieldForm.name, string: this.listenerFieldForm.string }
          : {
              name: this.listenerFieldForm.name,
              expression: this.listenerFieldForm.expression
            };
      if (this.listenerFiledIndex === -1) {
        if (this.listenerForm.fields) {
          this.listenerForm.fields.push(filedObj);
        } else {
          this.$set(this.listenerForm, "fields", [filedObj]);
        }
      } else {
        this.listenerForm.fields.splice(this.listenerFiledIndex, 1, filedObj);
      }
      this.initListenerFields();
      this.showListenerFieldForm = false;
    },
    removeListener(listener, index) {
      const removedListener = this.ownerListenersList.splice(index, 1);
      this.$emit("remove-listener", removedListener);
      this.$emit("change", this.ownerListenersList);
    },
    removeListenerField(field, index) {
      this.listenerForm.fields.splice(index, 1);
      const removedField = this.fieldsOfListener.splice(index, 1);
      this.$emit("remove-listener", removedField);
    }
  }
};
</script>
