<template>
  <div id="app">
    <my-process-designer
      :additional-model="labelEditing"
      process-type="camunda"
      @element-click="elementClick"
      @init-finished="initModeler"
    />
    <process-panel :bpmn-modeler="modeler" prefix="camunda" class="process-panel" />
  </div>
</template>

<script>
import translations from "@/translations";
import ProcessPanel from "../package/process-panel/ProcessPanel";

export default {
  name: "App",
  components: { ProcessPanel },
  data() {
    return {
      xmlString: "",
      modeler: null,
      translationsSelf: translations,
      labelEditing: [{ labelEditingProvider: ["value", ""] }]
    };
  },
  created() {
    // console.log(this.translationsSelf);
  },
  methods: {
    initModeler(modeler) {
      this.modeler = modeler;
      console.log(this.modeler);
      console.log(this.modeler.getDefinitions());
    },
    elementClick(element) {
      console.log(element);
    },
    ProcessChanged(xml) {
      this.xmlString = xml;
      console.log(xml);
    }
  }
};
</script>

<style lang="scss">
body {
  overflow: hidden;
  margin: 0;
  padding: 8px;
  box-sizing: border-box;
}
#app {
  width: 100%;
  height: calc(100vh - 16px);
  box-sizing: border-box;
  display: inline-grid;
  grid-template-columns: auto max-content;
}
.my-process-designer {
  overflow: auto;
}
body,
body * {
  /*  */
  &::-webkit-scrollbar-track-piece {
    background-color: #fff; /**/
    -webkit-border-radius: 0; /**/
  }
  &::-webkit-scrollbar {
    width: 10px; /**/
    height: 8px; /**/
  }
  &::-webkit-scrollbar-thumb:vertical {
    /**/
    height: 50px;
    background-color: rgba(153, 153, 153, 0.5);
    -webkit-border-radius: 4px;
    outline: 2px solid #fff;
    outline-offset: -2px;
    border: 2px solid #fff;
  }
  &::-webkit-scrollbar-thumb {
    /*hover*/
    background-color: rgba(159, 159, 159, 0.3);
    -webkit-border-radius: 4px;
  }
  &::-webkit-scrollbar-thumb:hover {
    /*hover*/
    background-color: rgba(159, 159, 159, 0.5);
    -webkit-border-radius: 4px;
  }
}
</style>
